body {
    background-color:#282C34;
    color: white;
  }
  
  .container {
    display: flex;
    align-items: center;
    flex-direction: column;
  }
  
  .image {
    width: 370px;
    margin-top: 50px;
  }
  
  @media only screen and (max-width: 850px) {
    .container {
      margin: 0 10%;
    }
  
    .image {
      width: 100%;
    }
  }